import React, { Component } from "react";
import { Form, InputNumber, Select } from "antd";
import EditableContext from "./editableContext";
import IntlMessages from "../../../../components/utility/intlMessages";
import NumberFormat from "../../../App/NumberFormat";

const FormItem = Form.Item;
const Option = Select.Option;

class EditableCell extends Component {
  state = {
    editing: false,
    err: null
  };

  toggleEdit = () => {
    const editing = !this.state.editing;

    this.setState({ editing }, () => {
      if (editing) {
        this.input.focus();
      }
    });
  };

  handleClickOutside = e => {
    const { editing } = this.state;

    if (editing && !this.cell) {this.toggleEdit();} else if (editing && this.cell !== e.target && !this.cell.contains(e.target)) {
      this.save();
      document.removeEventListener("click", this.handleClickOutside, true);
    }
  };

  save = (name, id) => {
    const { record, handleSave, job_costing_details, status } = this.props;

    this.form.validateFields((error, values) => {
      if (error) {
        return;
      }

      if(status === 1 ){ 
        //console.log(job_costing_details, "masuk")
        let item = job_costing_details.find((x) => x.id_item === record.id_item);

        if (values.base_qty > item.current_qty) {
          this.setState({
            err: 
            <IntlMessages 
              id="insufficientStock" 
              values={{ 
                qty: parseFloat(item.current_qty)
               }}
            /> 
          });
          return;
        }
        else{
          this.setState({
            err: null 
          });
          //return;
        }
      }
    
      this.toggleEdit();

      handleSave({ ...record, ...values });
    });
  };

  render() {
    const { editing, err } = this.state;
    const {
      editable,
      dataIndex,
      title,
      record,
      index,
      handleSave,
      status,
      ...restProps
    } = this.props;
    //console.log(record);
    return (
      <td ref={node => (this.cell = node)} {...restProps}>
        {editable ? (
          <EditableContext.Consumer>
            {form => {
              this.form = form;
              if (editing && dataIndex === "base_qty" && status === 1) {
                document.addEventListener(
                  "click",
                  this.handleClickOutside,
                  true
                );
                return (
                  <FormItem 
                    style={{ margin: 0 }}
                    validateStatus={
                      err || form.getFieldError(dataIndex) ? "error" : null
                    }
                    help={err ? err : form.getFieldError(dataIndex)}
                  >
                    {form.getFieldDecorator(dataIndex, {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ],
                      initialValue: record[dataIndex]
                    })(
                      <InputNumber
                        ref={node => (this.input = node)}
                        style={{ width: "100%" }}
                        onPressEnter={this.save}
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                        //max={record['id_job_costing_detail'] ? 9999999999999 : parseFloat(record['current_qty'])}
                        min={0.01}
                        precision={2}
                      />
                    )}
                  </FormItem>
                );
              } else if (!editing && dataIndex === "base_qty" && status === 1) {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {record[dataIndex]
                      ? <NumberFormat value={record[dataIndex]} />
                      : "0"}
                  </div>
                );
              } else if (!editing && dataIndex === "base_qty" && status !== 1) {
                return record[dataIndex]
                  ? <NumberFormat value={record[dataIndex]} />
                  : "0"
              } else if (status !== 1) {
                return restProps.children;
              } else {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {restProps.children}
                  </div>
                );
              }
            }}
          </EditableContext.Consumer>
        ) : (
          restProps.children
        )}
      </td>
    );
  }
}

export default EditableCell;
