import React, { Component } from "react";

import { Button, Popconfirm, Tooltip, Form } from "antd";
import EditableCell from "./editableCell";
import EditableContext from "./editableContext";
import "../../../App/editableStyle.css";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import NumberFormat from "../../../App/NumberFormat";
import CardWrapper from "../../../App/app.style";
import ModalDetail from "./modalDetail";
import getLanguage from "../../../App/MyLanguange";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

const getTotal = arr => {
  let totalAmount = 0;
  arr.map(data => {
    totalAmount += parseFloat(data.cost_per_unit) * parseFloat(data.base_qty);
  });
  return {
    totalAmount,
  };
};

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { job_costing_details } = props;

    this.state = {
      dataSource: [...job_costing_details],
      count: job_costing_details.length + 1,
      totalAmount: 0,
      visible: false,
      selectedItem: {}
    };

  }

  componentDidMount() {
    const { 
      temp_job_costing_detail, 
      temp_job_costing_details, 
      clearJobCostingDetail, 
      clearJobCostingDetails,
      clear
    } = this.props;

    // milih expense sendiri
    if (temp_job_costing_detail) {
      const {
        dataSource,
        count,
        totalAmount,
      } = this.state;

      const find = dataSource.findIndex(
        currData => currData.id_item === temp_job_costing_detail.id_item
      );

      // expense baru
      if (find < 0) {
        const init = {
          index_no: count,
          amount: 0,
          base_qty: 1,
          current_qty:temp_job_costing_detail.current_qty,
          cost_per_unit: temp_job_costing_detail.item_cost 
            ? parseFloat(temp_job_costing_detail.item_cost)
            : 0,
          sub_total: temp_job_costing_detail.item_cost 
            ? parseFloat(temp_job_costing_detail.item_cost)
            : 0,
          base_unit_code: temp_job_costing_detail.base_unit.unit_code,
          id_department: null,
          id_project: null
        };

        const newData = Object.assign({}, temp_job_costing_detail, init);
        // console.log(newData);

        

        this.setState({
          dataSource: [...dataSource, newData],
          count: count + 1,
        });

        let totalAmountAfter = getTotal([...dataSource, newData]).totalAmount;
        this.setState({
          totalAmount: totalAmountAfter
        })

        clearJobCostingDetail(
          //[...dataSource, newData],
          dataSource,
          totalAmountAfter
        );
        // console.log(this.props.expense);
      }

      else if (find >= 0) {
        let totalAmount = 0;
        const updateData = dataSource;
        let newData = Object.assign({}, updateData[find]);

        newData.base_qty = parseFloat(newData.base_qty) + 1;
        newData.sub_total = parseFloat(newData.base_qty) * parseFloat(newData.cost_per_unit)

        updateData.splice(find, 1, {
          ...updateData[find],
          ...newData
        });

        updateData.map(
          currData => (totalAmount += parseFloat(currData.sub_total))
        )

        this.setState({
          dataSource: [...updateData],
          totalAmount
        });

        clearJobCostingDetail(updateData, totalAmount);
      }
    }
    // jika temp dari transaksi sebelumnya
    else if (temp_job_costing_details.length !== 0) {
      let newTotAmount = 0;
      let newIndex = 0;

      temp_job_costing_details.map(currData => {
        newTotAmount += parseFloat(currData.cost_per_unit) * parseFloat(currData.base_qty);
        newIndex = parseInt(currData.index_no) + 1;
      });

      this.setState({
        dataSource: [...temp_job_costing_details],
        totalAmount: newTotAmount,
        count: newIndex
      });
      clearJobCostingDetails(temp_job_costing_details, newTotAmount);
    }

    else if(clear){
      this.setState({
        dataSource: [],
        totalAmount: 0,
        count: 1,
      });
      clearJobCostingDetails([], 0);
    }

  }
  

  componentDidUpdate(prevProps) {
    const { 
      temp_job_costing_detail, 
      temp_job_costing_details, 
      clearJobCostingDetail, 
      clearJobCostingDetails,
      clear
    } = this.props;

    // milih expense sendiri
    if (temp_job_costing_detail) {
      const {
        dataSource,
        count,
        totalAmount,
      } = this.state;

      const find = dataSource.findIndex(
        currData => currData.id_item === temp_job_costing_detail.id_item
      );

      // expense baru
      if (find < 0) {
        const init = {
          index_no: count,
          amount: 0,
          base_qty: 1,
          current_qty:temp_job_costing_detail.current_qty,
          cost_per_unit: temp_job_costing_detail.item_cost 
            ? parseFloat(temp_job_costing_detail.item_cost)
            : 0,
          sub_total: temp_job_costing_detail.item_cost 
            ? parseFloat(temp_job_costing_detail.item_cost)
            : 0,
          base_unit_code: temp_job_costing_detail.base_unit.unit_code,
          id_department: null,
          id_project: null
        };

        const newData = Object.assign({}, temp_job_costing_detail, init);
        // console.log(newData);

        

        this.setState({
          dataSource: [...dataSource, newData],
          count: count + 1,
        });

        let totalAmountAfter = getTotal([...dataSource, newData]).totalAmount;
        // console.log(totalAmountAfter)
        this.setState({
          totalAmount: totalAmountAfter
        })

        clearJobCostingDetail(
          [...dataSource, newData],
          //dataSource,
          totalAmountAfter
        );
        // console.log(this.props.expense);
      }
      else if (find >= 0) {
        let totalAmount = 0;
        const updateData = dataSource;
        let newData = Object.assign({}, updateData[find]);

        newData.base_qty = parseFloat(newData.base_qty) + 1;
        newData.sub_total = parseFloat(newData.base_qty) * parseFloat(newData.cost_per_unit)

        updateData.splice(find, 1, {
          ...updateData[find],
          ...newData
        });

        updateData.map(
          currData => (totalAmount += parseFloat(currData.sub_total))
        )

        this.setState({
          dataSource: [...updateData],
          totalAmount
        });

        clearJobCostingDetail(updateData, totalAmount);
      }
    }
    // jika expense dari transaksi sebelumnya
    else if (temp_job_costing_details.length !== 0) {
      let newTotAmount = 0;
      let newIndex = 0;

      temp_job_costing_details.map(currData => {
        newTotAmount += parseFloat(currData.cost_per_unit) * parseFloat(currData.base_qty);
        newIndex = parseInt(currData.index_no) + 1;
      });

      this.setState({
        dataSource: [...temp_job_costing_details],
        totalAmount: newTotAmount,
        count: newIndex
      });
      clearJobCostingDetails(temp_job_costing_details, newTotAmount);
    }
    else if(clear){
      this.setState({
        dataSource: [],
        totalAmount: 0,
        count: 1,
      });
      clearJobCostingDetails([], 0);

    }
  }

  handleDetailModal = row => {
    const { visible } = this.state;
    // console.log(row);

    visible
      ? this.setState({ visible: false, selectedItem: {} })
      : this.setState({ visible: true, selectedItem: row });
  };

  handleDetail = value => {
    this.setState({ visible: false, selectedItem: {} });
    this.handleSave(value);
  };

  handleDelete = index_no => {
    const { handleSave } = this.props;
    let newTotAmount = 0;

    const dataSource = this.state.dataSource;

    let newData = dataSource.filter(currData => currData.index_no !== index_no);
    newData.map((currData, index) => {
      currData.index_no = index + 1;
      newTotAmount += parseFloat(currData.cost_per_unit) * parseFloat(currData.base_qty);
    });

    this.setState({
      dataSource: [...newData],
      count: newData.length + 1,
      totalAmount: newTotAmount,
    });
    handleSave(newData, newTotAmount);
  };

  handleSave = row => {
    const newData = this.state.dataSource;
    let newTotAmount = 0;

    const index = newData.findIndex(
      currData => row.index_no === currData.index_no
    );

    //console.log(index);

    row.sub_total = parseFloat(row.cost_per_unit) * parseFloat(row.base_qty);

    // console.log(newData);

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    // console.log(newData)

    newData.map(currData => {
      newTotAmount += parseFloat(currData.sub_total);
    });


    this.setState({
      dataSource: [...newData],
      totalAmount: newTotAmount,
    });
    this.props.handleSave(newData, newTotAmount);
  };

  render() {

    let columns = [
      {
        title: getLanguage({id: "no."}),
        dataIndex: "index_no",
        width: "5%",
        align: "right",
        render: (text, row) => {
          if (isNaN(row.cost_per_unit)) {
            return {
              //children: text,
              props: {
                colSpan: 1
              }
            };
          } else {
            return {
              children: text,
              props: {
                colSpan: 1
              }
            };
          }
        }
      },
      {
        title: getLanguage({id: "items.code"}),
        dataIndex: "item_code",
        width: "15%",
        // fixed: "left",
        render: text => {
          if (text && text.length > 10) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 10) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({id: "itemName"}),
        dataIndex: "item_name",
        width: "30%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({id: "qty"}),
        dataIndex: "base_qty",
        width: "10%",
        align: "right",
        onCell: record => {
          if(isNaN(record.cost_per_unit)){
            return {

            }
          }
          else{
            return {
              record,
              editable: true,
              dataIndex: "base_qty",
              title: getLanguage({id: "qty"}),
              status: this.props.status,
              job_costing_details: this.props.job_costing_details ? this.props.job_costing_details : [],     
              handleSave: this.handleSave
            }   
          }
        }
      },
      {
        title: getLanguage({id: "sidebar.unit"}),
        width: "10%",
        dataIndex: "base_unit_code",
        align: "center",
        //editable: true
      },
      {
        title: getLanguage({id: "cost"}),
        dataIndex: "cost_per_unit",
        width: "15%",
        align: "right",
        //editable: true,
        render: text => {
          if (isNaN(text)) {
            return <span> {text} </span>
          } 
          else 
          {
            return <NumberFormat value={text} />
            
          }
        }
      },
      {
        title: getLanguage({id: "subTotal"}),
        dataIndex: "sub_total",
        width: "20%",
        // editable: true,
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: "",
        dataIndex: "operation",
        width: "15%",
        align: "center",
        fixed: "right",
        render: (text, row) => {
          if (isNaN(row.cost_per_unit)) {
            return {
              props: {
                colSpan: 1
                // fixed: null
              }
            };
          }
          else if (!this.props.status || this.props.status === 1) {
            return (
              <div>
                <Tooltip
                  placement="top"
                  title={<IntlMessages id="detail" />}
                >
                  <Button
                    //style={{ borderRadius: "50%" }}
                    className="invoiceEdtBtn"
                    size="small"
                    onClick={() => this.handleDetailModal(row)}
                  >
                    <i className="ion-navicon-round" />
                  </Button>
                </Tooltip>
                <Popconfirm
                  title={<IntlMessages id="confirmDelete" />}
                  okText={<IntlMessages id="yes" />}
                  cancelText={<IntlMessages id="no" />}
                  onConfirm={() => this.handleDelete(row.index_no)}
                >
                  <Tooltip
                    title={<IntlMessages id="delete" />}
                  >
                    <Button
                      //style={{ borderRadius: "50%" }}
                      className="invoiceDltBtn"
                      size="small"
                    >
                      <i className="ion-trash-b" />
                    </Button>
                  </Tooltip>
                </Popconfirm>
              </div>
            );
          } else {
            return (
              <Tooltip
                placement="top"
                title={<IntlMessages id="detail" />}
              >
                <Button
                  //style={{ borderRadius: "50%" }}
                  className="invoiceEdtBtn"
                  size="small"
                  onClick={() => this.handleDetailModal(row)}
                >
                  <i className="ion-navicon-round" />
                </Button>
              </Tooltip>
            );
          }
        }
      },
    ];

    const { dataSource, visible, selectedItem } = this.state;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const total = getTotal(dataSource);
    //console.log(total);
    const info = {
      index_no: 0,
      cost_per_unit: "Total",
      sub_total: total.totalAmount
    };
    const modifiedData = dataSource.length > 0 ? [...dataSource, info] : [];
    // console.log(modifiedData, total);
    return (
      <CardWrapper>
        <div>
          <ModalDetail
            visible={visible}
            selectedData={selectedItem}
            status={this.props.status}
            handleDetailModal={this.handleDetailModal}
            handleDetail={this.handleDetail}
            submit={this.handleDetail}
            departments={this.props.departments}
            projects={this.props.projects}
          />

          <Scrollbars style={{ width: "100%" }}>
            <TableWrapper
              className="invoiceListTable"
              components={components}
              rowKey={record => record.index_no}
              rowClassName={() => "editable-row"}
              dataSource={modifiedData}
              columns={columns}
              pagination={false}
              // scroll={{ x: 1300 }}
            />
          </Scrollbars>
        </div>
      </CardWrapper>
    );
  }
}

export default EditableTable;
