import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import JobCostingCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/inventory/jobCosting/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";


const TabPane = Tabs.TabPane;

const redirectTo = "/dashboard/inventories/job-costings"

class CreateJobCosting extends Component {
  // Get All data diawal
  componentDidMount() {
    const { createJobCosting, getImportJobCostings } = this.props;
    createJobCosting();
    // getImportJobCostings();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.jobCosting.error) {
      notification("error", this.props.jobCosting.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.jobCosting.success) {
      this.props.history.replace(redirectTo + "/update/" + this.props.jobCosting.id_job_costing)
    }
  }

  handleSubmit = value => {
    const { storeJobCosting } = this.props;
    //console.log(value)
    storeJobCosting(value);
  };

  render() {
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    const { getImportJobCostings } = this.props;

    // console.log(permissions);
    
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="jobCostings.add.title" />}
            style={{ margin:0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="jobCostings.tab.detail" />} 
              key="1" 
            >
              <JobCostingCreateForm
                submit={this.handleSubmit}
                jobCosting={this.props.jobCosting}
                permissions={permissions}
                getItems={this.props.getItems}
                user={user}
                getImportJobCostings={getImportJobCostings}
              />    
            </TabPane>
            <TabPane tab={<IntlMessages id="jobCostings.tab.attachment" />} 
              key="2"
              disabled
            >  
            </TabPane>
          </Tabs>
          
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { 
    jobCosting: state.jobCosting,
    auth: state.auth
   };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateJobCosting);
