import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/inventory/jobCosting/actions";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import NumberFormat from "../../../App/NumberFormat";

import {
  Modal,
  Tooltip,
  Table,
  Collapse,
  Select,
  DatePicker,
  Input,
  Row,
  Col,
  Empty
} from "antd";
import {
  StatusTag,
  StatusBadge,
  TransNo,
  customCollapseStyle
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;
const Search = Input.Search;
const { getImportJobCostings } = actions;

const expandedRowRender = (job_costing_details, expenses) => {
  const jobCostingDetailsColumns = [
    {
      title: getLanguage({id: "no."}),
      dataIndex: "index_no",
      width: "5%"
    },
    {
      title: getLanguage({id: "items.code"}),
      dataIndex: "item_code",
      width: "15%",
      // fixed: "left",
      render: text => {
        if (text && text.length > 10) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 10) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({id: "itemName"}),
      dataIndex: "item_name",
      width: "20%",
      render: text => {
        if (text && text.length > 25) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 25) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({id: "qty"}),
      dataIndex: "base_qty",
      width: "5%",
      align: "right",
      render: text => <NumberFormat value={text} />
    },
    {
      title: getLanguage({id: "sidebar.unit"}),
      width: "5%",
      dataIndex: "base_unit_code",
      align: "center",
      //editable: true
    },
    {
      title: getLanguage({id: "cost"}),
      dataIndex: "cost_per_unit",
      width: "15%",
      align: "right",
      render: text => <NumberFormat value={text} />
    },
    {
      title: getLanguage({id: "subTotal"}),
      dataIndex: "sub_total",
      width: "15%",
      align: "right",
      render: (text, row) => <NumberFormat value={parseFloat(row.base_qty * row.cost_per_unit)} />
    },
  ];

  const expensesColumns = [
    {
      title: getLanguage({id: "no."}),
      dataIndex: "index_no",
      width: "5%"
    },
    {
      title: getLanguage({id: "accountCode"}),
      dataIndex: "account_code",
      width: "30%",
      render: text => {
        if (text && text.length > 25) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 25) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({id: "accountName"}),
      dataIndex: "account_name",
      width: "45%",
      render: (text, row) => {
        if (isNaN(row.index_no)) {
          return {
            props: {
              colSpan: 0
            }
          };
        }
        if (text && text.length > 50) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 50) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({id: "baseAmount"}),
      dataIndex: "amount",
      width: "20%",
      align: "right",
      render: text => <NumberFormat value={text} />
    },
  ];

  return (
    <div>
      <strong>
        <IntlMessages id="itemDetails" />
      </strong>

      <Table
        rowKey={record => record.index_no}
        size="small"
        columns={jobCostingDetailsColumns}
        dataSource={job_costing_details}
        pagination={true}
        style={{ marginTop: "24px" }}
      />

      { expenses.length > 0 
        ? 
          (
            <>
              <strong>
                <IntlMessages id="expenseDetails" />
              </strong>

              <Table
                rowKey={record => record.index_no}
                size="small"
                columns={expensesColumns}
                dataSource={expenses}
                pagination={true}
                style={{ marginTop: "24px" }}
              />
            </>
          )
        : null

      }

    </div>
  );
};

class ModalJobCosting extends Component {
  state = {
    filter: {
      id_location: null,
      status: null,
      start_date: null,
      end_date: null,
      pagination: null
    },
    search: null
  };

  handleSelect = value => {
    const { handleSelectModal } = this.props;
    handleSelectModal(value);
  };

  handleLocationChange = id_location => {
    const { getImportJobCostings } = this.props;
    const string = "id_location=" + id_location;
    getImportJobCostings(1, null, string);
  };

  componentDidUpdate(prevProps) {
    const { id_location } = this.props;

    if (prevProps.id_location !== id_location) {
      this.handleLocationChange(id_location);
      this.setState({
        filter: {
          ...this.state.filter,
          id_location: id_location
        }
      });
    }
  }

  // Handle search
  handleSearch = value => {
    const { getImportJobCostings } = this.props;
    this.setState({
      filter: {
        id_location: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    getImportJobCostings(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getImportJobCostings } = this.props;
    this.setState({
      filter: {
        id_location: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getImportJobCostings();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  // Handle filter
  handleFilter = () => {
    const { getImportJobCostings } = this.props;
    //const { current_page } = this.props.jobCosting;
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.status) {
      string
        ? (string += "&status=" + filter.status)
        : (string += "status=" + filter.status);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }

    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getImportJobCostings(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getImportJobCostings } = this.props;
    this.setState({
      filter: {
        id_location: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getImportJobCostings();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateJobCosting(id);
    } else {
      this.props.deactivateJobCosting(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getImportJobCostings } = this.props;
    const { import_search, import_filter } = this.props.jobCosting;
    getImportJobCostings(page, import_search, import_filter);
  };

  // Definisi kolom2 serta field2 API yang dipakai tabel
  columns = [
    {
      title: getLanguage({id : "jobCostings.no"}),
      dataIndex: "job_costing_no",
      rowKey: "job_costing_no",
      width: "20%",
      render: (text, row) => {
        let className;
        switch (row.status) {
          case 1:
            className = "new";
            break;
          case 2:
            className = "ordered";
            break;   
          case 3:
            className = "cancelled";
            break;
          case 4:
            className = "closed";
            break;
          default:
            className = "new";
            break;
        }

        return (
          <div style={{ display: "flex", height: "20px" }}>
            <StatusBadge className={className} />
            <Tooltip
              placement="bottom"
              title={<IntlMessages id="tooltip.select" />}
            >
              <a
                onClick={e => {
                  e.preventDefault();
                  this.handleSelect(row.id_job_costing);
                }}
                href="javascript:;"
              >
                {text ? <TransNo>{text}</TransNo> : <TransNo>{getLanguage({id : "new"})}</TransNo>}
              </a>
            </Tooltip>
          </div>
        );
      }
    },
    {
      title: getLanguage({id : "date"}),
      dataIndex: "transaction_date",
      rowKey: "transaction_date",
      width: "15%",
      render: text => (
        <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
      )
    },
    {
      title: getLanguage({id : "sidebar.location"}),
      dataIndex: "location.location_name",
      rowKey: "location.location_name",
      width: "25%",
      render: text => {
        if (text && text.length > 25) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 25) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({id : "total"}),
      dataIndex: "total_amount",
      rowKey: "total_amount",
      width: "10%",
      align: "right",
      render: text => <NumberFormat value={text} /> 
    },
    {
      title: getLanguage({id : "createdBy"}),
      dataIndex: "created_by",
      rowKey: "created_by",
      width: "10%",
      render: text => {
        if (text && text.length > 25) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 25) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({id : "finishedBy"}),
      dataIndex: "finished_by",
      rowKey: "finished_by",
      width: "10%",
      render: text => {
        if (text && text.length > 25) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 25) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({id : "status"}),
      dataIndex: "status",
      rowKey: "status",
      width: "15%",
      align: "center",
      render: text => {
        let className;
        let display;
        switch (text) {
          case 1:
            className = "new";
            display = getLanguage({id : "new"});
            break;
          case 2:
            className = "ordered";
            display = getLanguage({id : "processed"});
            break;
          // case 2:
          //   className = "closed";
          //   display = "Processed";
          //   break;
          case 3:
            className = "cancelled";
            display = getLanguage({id : "cancelled"});
            break;
          case 4:
            className = "closed";
            display = getLanguage({id : "finished"});
            break;
          default:
            className = "new";
            display = getLanguage({id : "new"});
            break;
        }

        return <StatusTag className={className}>{display}</StatusTag>;
      }
    }
  ];

  render() {
    // console.log(this.props);

    const { visible, handleModal, jobCosting, id_location } = this.props;
    const { import_job_costings, locations } = jobCosting
      ? jobCosting
      : [];
    const { loadingImport } = jobCosting ? jobCosting : false;
    const dataSource = [];
    Object.keys(import_job_costings).map((job_costing, index) => {
      return dataSource.push({
        ...import_job_costings[job_costing],
        key: job_costing
      });
    });

    const {
      import_current_page,
      import_page_size,
      import_total
    } = this.props.jobCosting;

    return (
      <Modal
        title={<IntlMessages id="jobCostings.copy" />}
        visible={visible}
        centered
        footer={null}
        keyboard={true}
        onCancel={handleModal}
        width="95%"
      >
        <Row style={{ marginBottom: "10px" }} type="flex">
          <Col span={18}>
            <Search
              placeholder={
                getLanguage({id : "jobCostings.no"}) + ", " +
                getLanguage({id : "createdBy"})
              }              
              onSearch={this.handleSearch}
              style={{ width: "99%", height: "36px" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
          </Col>
          <Col span={6}>
            <Button
              type="primary"
              onClick={this.handleSearch.bind(this, this.state.search)}
              style={{ marginRight: "2%" }}
            >
              <IntlMessages id="button.search" />
            </Button>

            <Button onClick={this.handleResetSearch}>
              <IntlMessages id="filter.reset" />
            </Button>
          </Col>
        </Row>
        <Row style={{ marginBottom: "10px" }}>
          <Col>
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="jobCostings.filter.select.location" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingImport}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="jobCostings.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={true}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                </Row>
                <Row
                  type="flex"
                  justify="start"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={4}>
                    <label>Status</label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(this, "status")}
                      value={
                        this.state.filter.status ? this.state.filter.status : []
                      }
                      style={{ width: "90%" }}
                      placeholder="Status"
                      optionFilterProp="children"
                      filterOption={true}
                      // loading={this.props.location.loadingFilterSites}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {/* <Option value={1}>{getLanguage({id : "new"})}</Option> */}
                      <Option value={2}>{getLanguage({id : "processed"})}</Option>
                      <Option value={3}>{getLanguage({id : "cancelled"})}</Option>
                      <Option value={4}>{getLanguage({id : "finished"})}</Option>
                    </Select>
                  </Col>
                {/* </Row>
                <Row type="flex" justify="start"> */}
                  <Col span={4}>
                    <label>
                      <IntlMessages id="dataPerPage" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "pagination"
                      )}
                      value={
                        this.state.filter.pagination
                          ? this.state.filter.pagination
                          : import_page_size
                      }
                      style={{ width: "80%" }}
                      // placeholder={
                      //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                      // }
                      optionFilterProp="children"
                      filterOption={true}
                      //allowClear={true}
                      //showArrow={false}
                    >
                      <Option key={1} value={10}>
                        10
                      </Option>
                      <Option key={2} value={25}>
                        25
                      </Option>
                      <Option key={3} value={50}>
                        50
                      </Option>
                      <Option key={4} value={100}>
                        100
                      </Option>
                    </Select>
                  </Col>
                  <Col
                    span={4}
                    style={{
                      marginTop: "auto"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </Col>
        </Row>

        <Row>
          <Col>
            {dataSource.length === 0 && !loadingImport ? (
              <Empty
                description={<IntlMessages id="jobCostings.table.noData" />}
              />
            ) : (
              <Table
                dataSource={dataSource}
                columns={this.columns}
                loading={loadingImport}
                expandedRowRender={record =>
                  expandedRowRender(record.job_costing_details, record.expenses)
                }
                expandIcon={props => (
                  <Button
                    onClick={e => props.onExpand(props.record, e)}
                    size="small"
                  >
                    <IntlMessages id="button.view" />
                  </Button>
                )}
                pagination={
                  import_page_size
                    ? {
                        current: import_current_page,
                        pageSize: parseInt(import_page_size),
                        total: import_total,
                        onChange: this.handlePageChange.bind(this)
                      }
                    : true
                }
              />
            )}
          </Col>
        </Row>
      </Modal>
    );
  }
}

export default compose(
  connect(
    null,
    { getImportJobCostings }
  )
)(ModalJobCosting);
