const actions = {
  getJobCostings: (page, search, filter) => ({
    type: 'GET_JOB_COSTINGS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getImportJobCostings: (page, search, filter) => ({
    type: 'GET_IMPORT_JOB_COSTINGS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getItems: (id_location, search) => ({
    type: "GET_FILTER_ITEMS_JC_REQUEST",
    payload: { id_location: id_location, search: search }
  }),
  createJobCosting: () => ({
    type: 'CREATE_JOB_COSTING_REQUEST'
  }),
  storeJobCosting: data => ({
    type: 'STORE_JOB_COSTING_REQUEST',
    payload: { body: data }
  }),
  editJobCosting: id_job_costing => ({
    type: 'EDIT_JOB_COSTING_REQUEST',
    payload: { id_job_costing: id_job_costing }
  }),
  updateJobCosting: (id_job_costing, data) => ({
    type: 'UPDATE_JOB_COSTING_REQUEST',
    payload: {
      id_job_costing: id_job_costing,
      body: data
    }
  }),
  showJobCosting: id_job_costing => ({
    type: 'SHOW_JOB_COSTING_REQUEST',
    payload: { id_job_costing: id_job_costing }
  }),
  processJobCosting: id_job_costing => ({
    type: "PROCESS_JOB_COSTING_REQUEST",
    payload: { id_job_costing }
  }),
  cancelJobCosting: (id_job_costing, data) => ({
    type: "CANCEL_JOB_COSTING_REQUEST",
    payload: { 
      id_job_costing,
      body : data
    }
  }),
  uploadDocument: (id_job_costing, data) => ({
    type: "UPLOAD_JOB_COSTING_DOCUMENT_REQUEST",
    payload: {
      id_job_costing,
      body : data
    }
  }),
  downloadDocument: id_job_costing_document => ({
    type: "DOWNLOAD_JOB_COSTING_DOCUMENT_REQUEST",
    payload: {id_job_costing_document}
  }),
  deleteDocument: id_job_costing_document => ({
    type: "DELETE_JOB_COSTING_DOCUMENT_REQUEST",
    payload: { id_job_costing_document }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
